'use client';
import React from 'react';
import Link from 'next/link';

type GlassContainerProps = {
  date?: string;
  title?: string;
  description?: string;
  link: string;
  tags?: string;
}

export default function GlassContainer(props: GlassContainerProps)
{
  return (
    <Link href={props.link} target="_blank"
      className='
      opacity-55
      bg-white/5
      rounded-[9] 
      transition duration-100 ease-in-out
      border-t border-t-white/30

      hover:bg-white/6
      hover:opacity-200
      hover:shadow-[0_10px_20px_rgba(0,0,0,0.1)]
      hover:backdrop-blur-md
      hover:border-t-white/30

      flex gap-10 justify-start p-6'>
      <div className=" flex gap-10 justify-start">
        <div className="text-sm opacity-70 text-white">
          {props.date}
        </div>
        <div className="flex flex-col gap-5">
          <h1 className="text-xl font-semibold text-white">{props.title}</h1>
          <p className="text-gray-300">{props.description}</p>
        </div>
      </div>
    </Link>
  );
}
